body {
    width: 100%;
    height: 100%;
    position: absolute;
    color: $color-font;
    font-family: $font-primary;
    -webkit-font-smoothing: antialiased;
}

input:focus, textarea:focus, select:focus{
outline: none;
}

::selection {
  background: $color-primary; 
}

::-moz-selection {
  background: $color-primary; 
}


@mixin flex-flow($value) {
    -webkit-flex-flow: $value;
    -moz-flex-flow: $value;
    -ms-flex-flow: $value;
    -o-flex-flow: $value;
    flex-flow: $value;
}

.align_auto {
    align-self: auto;
}

.align_flex-start {
    align-self: flex-start;
}

.align_flex-end {
    align-self: flex-end;
}

.align_baseline {
    align-self: baseline;
}

.align_stretch {
    align-self: stretch;
}

.align_h_center {
    align-items: center;
    text-align: center;
}

.align_h_left {
    align-items: flex-start;
    text-align: left;

    &--responsive {
        @extend .align_h_left;
    }

    @media(max-width: $width-desktop) {
        &--responsive {
            align-items: center;
            text-align: center;
        }
    }
}

.align_h_right {
    align-items: flex-end;
    text-align: right;

    &--responsive {
        @extend .align_h_right;
    }

    @media(max-width: $width-desktop) {
        &--responsive {
            align-items: center;
            text-align: center;
        }
    }
}

.display-flex {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
}

.center {
    text-align: center;
    align-items: center;
    justify-content: center;
}

.paragraph {
    font-family: $font-primary;
    color: $color-font;
    line-height: 150%;
    margin-bottom: 10px;
}

.lite {
    color: #ffffff;
}

.container {
    @extend .display-flex;
    @include flex-flow(wrap);
    margin: auto;
    width: 98%;
    max-width: $max-width-container;
    min-width: $min-width-container;

    &--header {
        @extend .container;
        max-width: 99%;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    &--body {
        @extend .container;
        max-width: 960px;
    }
}

.form{
 @extend .display-flex;
  @include flex-flow(wrap);
}

.textbox{
    border:0;
    border-bottom: 1px solid $color-secundary;
    color: $color-secundary;
    padding: 10px;
    background-color: transparent;
    width: 90%;
    display: inline-block;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
    &--mensagem{
        @extend .textbox;
    }
}

.textbox:focus{
     border-bottom: 1px solid $color-primary;
}

.textbox:hover{
     border-bottom: 1px solid $color-primary;
}

.scroll {
    bottom: 0;
    left: 50%;
    position: fixed;
    z-index: 3;
    transform: translate( -50%,-50%);
    display: block;
}

.division{
    display: inline-block;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 70px;
    height: 1px;
    background-color: $color-secundary;
}


@keyframes arrow {
    0% {
        opacity: 0;
    }

    40% {
        opacity: 1;
    }

    80% {
        opacity: 0;
    }

    100% {
        opacity: 0;
    }
}

@-webkit-keyframes arrow /*Safari and Chrome*/
{
    0% {
        opacity: 0;
    }

    40% {
        opacity: 1;
    }

    80% {
        opacity: 0;
    }

    100% {
        opacity: 0;
    }
}

.arrows path.a1 {
    animation-delay: -1s;
    -webkit-animation-delay: -1s;
}

.arrows path.a2 {
    animation-delay: -0.5s;
    -webkit-animation-delay: -0.5s;
}

.arrows path.a3 {
    animation-delay: 0s;
    -webkit-animation-delay: 0s;
}


$breakpoints: ( (xs), (sm, $width-mobile), (md, $width-tablet), (lg, $width-desktop)) !default;

@function convert-col-percent($column-num) {
    @return $column-num / $max-col * 100%;
}

@mixin spacing( $value, $type: margin, $orientation: vertical) {
    @if $orientation==vertical {
        #{ $type }-top: $value;
        #{ $type }-bottom: $value;
    }
    @else {
        #{ $type }-left: $value;
        #{ $type }-right: $value;
    }
}

.#{ $row } {
    box-sizing: border-box;
    display: flex-wrap;
    flex-direction: row;
    flex-wrap: wrap;
    @include spacing( ( -$child), margin, horizontal);
}

%#{ $col } {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 0;
    @include spacing( ( $child), padding, horizontal);
}

%#{ $col }-flex {
    @extend %#{ $col };
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
}

@mixin grid-base( $namespace) {
    .#{ $col }-#{$namespace} {
        @extend %#{ $col }-flex;
    }

    @for $i from 1 through $max-col {
        .#{ $col }-#{$namespace}-#{$i} {
            @extend %#{ $col };
        }
    }
}

@mixin grid-sizes( $namespace) {
    @for $i from 1 through $max-col {
        .#{ $col }-#{$namespace}-#{$i} {
            flex-basis: convert-col-percent($i);
            max-width: convert-col-percent($i);
        }
    }

    @for $i from 1 through $max-col {
        @if ( $i !=$max-col) {
            .#{ $off }-#{$namespace}-#{$i} {
                margin-left: convert-col-percent($i);
            }
        }
    }
}

@each $val in $breakpoints {
    $namespace: nth($val, 1);
    @include grid-base( $namespace);
}

@each $val in $breakpoints {
    @if length($val)==1 {
        $namespace: nth($val, 1);
        @include grid-sizes( $namespace);
    }
    @else {
        $namespace: nth($val, 1);
        $size: nth($val, 2);

        @media only screen and (max-width: #{$size}) {
            @include grid-sizes( $namespace);
        }
    }
}

.load {
    @extend .display-flex;
    align-items: center;
    justify-content: center;
    background-color: $color-load;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10000;

    &__item {
        width: 250px;
    }

    &__item-a {
        font-family: $font-title;
        font-size: 80px;
        fill: none;
        stroke: $color-primary;
        stroke-dasharray: 150;
        stroke-dashoffset: 150;
        animation: dash 2s linear alternate infinite;
    }

    &__item-b {
        font-family: $font-title;
        font-size: 80px;
        fill: none;
        stroke: #ffffff;
        stroke-dasharray: 250;
        stroke-dashoffset: 250;
        animation: dash 2s linear alternate infinite;
    }

    &__item-c {
        font-family: $font-title;
        font-size: 80px;
        fill: none;
        stroke: $color-primary;
        stroke-dasharray: 150;
        stroke-dashoffset: 150;
        animation: dash 2s linear alternate infinite;
    }
}

@keyframes dash {

    to {
        stroke-dashoffset: 0;
    }
}

